import React, { Component } from 'react'
import { Panel, PanelHeader, FixedLayout, Tabs, TabsItem, Div, Button, Link, ActionSheet, ActionSheetItem } from '@vkontakte/vkui'
import MyGroups from '../components/MyGroups'
import MyDB from '../components/MyDB'

import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';
import Icon28Send from '@vkontakte/icons/dist/28/send';

export class MySubs extends Component {
  state = {
    activeTab: 'groups',
  }

  openMenu = () => {
    this.props.setPopout(
      <ActionSheet onClose={() => this.props.setPopout(null)}>
        <ActionSheetItem autoclose before={<Icon28AddOutline />} onClick={() => this.props.go((this.state.activeTab == 'groups' ? 'groups': 'settings'))}>
          Добавить подписку
        </ActionSheetItem>
        <ActionSheetItem autoclose before={<Icon28Send />} target='_blank' href={"http://www.aviasales.ru/?marker="+ this.props.state.marker}>
          Купить билет  
        </ActionSheetItem>
      </ActionSheet>
    )
  }

  render() {
    const {
      activeTab
    } = this.state
    const {
      id,
      state
    } = this.props
    return (
      <Panel id={id}>
        <PanelHeader>Подписки</PanelHeader>
        <FixedLayout vertical="top">
          <Tabs>
            <TabsItem
              selected={activeTab === 'groups'}
              onClick={() => {this.setState({ activeTab: 'groups' });}}
            >Витрина</TabsItem>
            <TabsItem
              selected={activeTab === 'database'}
              onClick={() => {this.setState({ activeTab: 'database' });}}
            >Мой выбор</TabsItem>
          </Tabs>
        </FixedLayout>

        <FixedLayout vertical='bottom'>
          <Div align="center" style={{ paddingBottom:6 }}><Button className='circle_button' onClick={this.openMenu}><Icon28AddOutline /></Button></Div>
        </FixedLayout>
        <Div />
        {
          activeTab === 'groups' ? 
            <MyGroups
              setPopout={this.props.setPopout}
              onChangeGroups={this.props.onChangeGroups}
              openModal={this.props.openModal}
              state={state}
            />
          :
            <MyDB
              onChangeGroups={this.props.onChangeGroups}
              setPopout={this.props.setPopout}
              state={state}
              onChange={this.props.onChange}
              go={this.props.go}
            />
        }
      </Panel>
    )
  }
}

export default MySubs
